import React from 'react';
import { Container, TextField, Button, Typography, Box, Paper} from '@mui/material';
import { useCreateBlogPostMutation } from '@/services';
import { Formik, useFormik } from 'formik';
import * as Yup from 'yup';
import { BlogPost } from '@/type';
import CustomInput from '@/Components/CustomInput';

const validationSchema = Yup.object({
  title: Yup.string().required('Title is required'),
  author: Yup.string().required('Author is required'),
  body: Yup.string().required('Body is required'),
});

const AddPost = () => {
  const [createBlogPost, { isLoading }] = useCreateBlogPostMutation();

  const formik = useFormik({
    initialValues: {
      title: '',
      author: '',
      body: '',
    },
    validationSchema,
    onSubmit: async (values, { resetForm }) => {
      const newPost : BlogPost = {
        title: values.title,
        author: values.author,
        excerpt: values.body,
        date: Date.now(),
      };

      try {
        const result = await createBlogPost(newPost).unwrap();
        console.log('Created:', result);
        resetForm();
      } catch (err) {
        console.error('Failed to create post:', err);
      }
    },
  });

  return (
    <Container maxWidth="sm" sx={{ mt: 4 }}>
      <Paper elevation={3} sx={{ p: 4 }}>
        <Typography variant="h5" align="center" gutterBottom>
          Add New Blog Post
        </Typography>

        <Formik
          onSubmit={(formData) => {
            console.log(formData);
          }}
        >
          {({ isSubmitting }) => (
    <Form>
      <Field type="text" name="fullname" placeholder="Enter fullname" />
      <Field type="email" name="email" placeholder="Enter address" />
      <button type="submit">Submit</button>
    </Form>
  )}

        </Formik>

        {/* <form onSubmit={formik.handleSubmit}>

        <CustomInput
          label="Title"
          name="title"
          value={formik.values.title}
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          error={Boolean(formik.touched.title && formik.errors.title)}
          />

        <CustomInput
          label="Author"
          name="author"
          value={formik.values.author}
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          error={Boolean(formik.errors.author)}
          />

          
        <CustomInput
          label="Body"
          name="body"
          value={formik.values.body}
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          error={Boolean(formik.errors.body)}
          multiline
          rows={6}
          />

          <Box textAlign="center" mt={3}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              disabled={isLoading}
            >
              {isLoading ? 'Submitting...' : 'Submit'}
            </Button>
          </Box>
        </form> */}
      </Paper>
    </Container>
  );
};

export default AddPost;
